<template>
	<div id="content">
		<div v-if="!isWeiXin" class="header">
			
			<img @click.stop="back" class="back" src="./back.png" alt="">
		</div>
		<div v-if="!isWeiXin" class="padding225"></div>
		<!--  -->
		<div class="clause_wrapper clearfix">
			<div class="clause">
				<br/>
				
			</div>
			<div class="clause">
				 
			</div>
			<div class="clause">
				1.27-45<br/>			
				2.
			</div>
			<div class="clause">
				3.
			</div>
			<div class="clause">
				4. <br/>
				5.,
			</div>
			<div class="clause">
				6.(95511)95589(95510)9554295308<br/>
				7.<br/>
				8.				
			</div>
			<div class="clause red">
				
				<br/>
				
			</div>
			<div class="clause red">
				 , 
			</div>
		</div>
	</div>
</template>
<script>
	import {isWeiXin} from '../../assets/js/public';
	export default{
		data(){
			return{
				isWeiXin:false // 
			}
		},
		methods:{
			back(){//
				this.$router.back(-1)
			}
		},
		created(){
			if(isWeiXin()){
				this.isWeiXin = true;
			}
			window.scrollTo(0,0);
		}
	}
</script>
<style scoped lang="less">
	#content{
		position:relative;
		width:100%;
		height:100%;
		background:url('./background_square.png');
		background-size:6px 6px;
		.padding225{
			width:100%;
			height:2.25rem;
		}
		.header{
			position:fixed;
			left:0;
			top:0;
			width:100%;
			height:2.25rem;
			line-height:2.25rem;
			font-size:.85rem;
			color:#000;
			text-align:center;
			background:#f0f0f0;
			img.back{
				position:absolute;
				left:.5rem;
				top:.65rem;
				width:.5rem;
				height:.85rem;
			}
		}
		.clause_wrapper{
			margin:0 auto;
			padding-top:.5rem;
			width:16.75rem;
			.clause{
				padding-bottom:.75rem;
				font-size:.6rem;
				line-height:.75rem;
				color:#000;
				text-align:left;
				&.red{
					color:#ff0707;
				}
			}
		}
	}
</style>


